<template>
  <n-popover trigger="hover">
    <template #trigger>
      <n-button color="#222" @click="test">{{ name }}</n-button>
    </template>
    {{ info }}
  </n-popover>
</template>

<script setup>
import { onMounted, ref } from "vue";

const props = defineProps({
  info: String,
  url: String,
});

const name = ref("");

onMounted(() => {
  let len = Math.floor(Math.random() * 10) + 1;
  name.value = new Array(len).fill("yes").join("");
});

const test = () => {
  window.open(props.url, "_blank");
};
</script>

<style>
</style>